import { Icon } from "leaflet"
import React, { Component } from "react"
import { Card, CardBody, CardTitle,Row,Col,Button, Progress } from "reactstrap"

import  { Redirect } from 'react-router-dom'

import url from "../../common/api"
class EvenementsCardAccueil extends Component {
  constructor(props) {
    super(props)
    this.state = {
      evenements:[],
      ok:"",
      nombreEvent:0
    }
    this.handleDetail.bind(this)
  }
  componentDidMount() {
    this.handleChargerData()
  }

    handleChargerData=()=>{
    var data=JSON.parse(localStorage.getItem("authAshia")).profile
   
      fetch(url+"profiles/"+data.id)
      .then((response) => response.json())
      .then((data) => {
       
        data.evenements.filter((val,id)=>id<=2).map((element,idx) =>{
          this.setState({evenements:[...this.state.evenements,element],nombreEvent:data.evenements.length} )
          
          
        })
      
        
        
       }
       
      );
  }
  handleAjouter=()=>{
    this.setState({ok: <Redirect to='/ajout-evenement'  />});
  }
  handleDetail=(id)=>{
    localStorage.setItem('idressource',id);
    this.setState({ok: <Redirect to='/detail-evenement'  />});
  }
  handleListe=()=>{
    this.setState({ok: <Redirect to='/liste-evenement'  />});
  }

  render() {
    return (
      <React.Fragment>
        <Card>         {this.state.ok}
          <CardBody>
            <CardTitle className="mb-4 h4">EVENEMENTS</CardTitle>
            <div className="text-center">
              <div className="mb-4">
                <i className="bx bx-calendar-event text-info display-5" style={{ lineHeight : '1'}}/>

                <h5>{this.state.nombreEvent}</h5>

              </div>
              
              <Row>
                <Col md="6" className="pull-right">
                  <Button
                    color="info"
                    className="font-16 btn-block btn btn-info  float-right"

                    onClick={this.handleAjouter}

                  >
                    <i className="mdi mdi-plus-circle-outline me-1" />
                    Crer un nouvel venement
                  </Button>
                </Col>

                {
                (this.state.evenements.length!=0)?
                (<Col md="6" className="pull-right">
                  <Button
                    color="info"
                    className="font-16 btn-block btn btn-info  float-right"
                    
                    onClick={this.handleListe}

                  >
                    <i className="mdi mdi-eye-outline me-1" />
                    Voir tous les venements
                  </Button>

                </Col>) :null
                }

              </Row>
            </div>

            <div className="table-responsive mt-4">
              <table className="table align-middle table-nowrap">
                <thead>

                {
                (this.state.evenements.length!=0)?
                  (<tr>

                    <th>
                      Titre
                    </th>
                    <th>
                      Date
                    </th>
                    <th>
                      Action
                    </th>

                  </tr>):(<tr>
                              <td style={{ width: "30%" }}>
                                <p className="mb-0">Il n'ya pas d'vnement</p>
                               </td>
                                        
                  </tr>)
                }
                </thead>

                <tbody>
               
                  {
                    (this.state.evenements.length!=0)?
                            (this.state.evenements.map(
                                 (element,idx)=>(
                                  <tr>
                                    <td style={{ width: "30%" }}>
                                      <p className="mb-0">{element.libelle}</p>
                                    </td>
                                    <td style={{ width: "25%" }}>
                                      <h5 className="mb-0">{element.date_debut}</h5>
                                    </td>
                                    <td>
                                      <a onClick={this.handleDetail.bind(this.bind,element.id)} > <i className="bx bxs-info-circle bx-sm text-info " /> </a>
                                    </td>
                                </tr>
                                )
                             )
                    ):null

                  } 

                </tbody>
              </table>
            </div>
          </CardBody>
        </Card>
      </React.Fragment>
    )
  }
}

export default EvenementsCardAccueil
