import React, { Component } from "react"
import PropTypes from 'prop-types'
import { Col, Modal, ModalBody, Row } from "reactstrap"
import { format } from 'date-fns';
import  { Redirect } from 'react-router-dom'

  class DetailModal extends Component {
    constructor(props) {
        super(props)
     
        this.state = {
          ok:"",

        }
  
        this.handleDetail.bind(this)
      }
      componentDidMount() {
       
      }
    handleDetail=(id)=>{
    localStorage.setItem('idressource',id);
    this.setState({ok: <Redirect to='/detail-evenement'  />});
    }

  render (){
  const show =this.props.show
  const onCloseClick=this.props.onCloseClick
  const event=this.props.event

  return (
    <Modal isOpen={show} toggle={onCloseClick} centered={true}>
      <ModalBody className="py-3 px-5">     {this.state.ok}
        <Row className="mb-3">
          <Col lg={12}>
            <div >
              <i className="bx bx-calendar-event text-info display-5" style={{ lineHeight : '1'}}/>
          
            </div>
          </Col>
        </Row>
        <Row className="mb-3">
          <Col lg={12}>
            <div>
             <div className="mb-2"><strong> Libelle:      </strong> <span>{event.libelle}</span></div>
             <div className="mb-2"><strong> Lieu:         </strong><span>{event.lieu}</span></div>
             <div className="mb-2"><strong> Dbut :       </strong> <span>{event.heure_debut}</span></div>
             <div className="mb-2"><strong> Fin :         </strong><span>{event.heure_fin}</span></div>
             <div className="mb-2"><strong> Description : </strong><span>{event.description}</span></div>
            </div>
          </Col>
        </Row>
        <Row>
          <Col>
            <div className="mt-3" style={{float:"left"}}>
              
              <button
                type="button"
                className="btn btn-info btn-sm me-2"
                href="/detail-evenement"
                onClick={this.handleDetail.bind(this.bind,event.id_event)}
              >
                Modifier l'venement
              </button>
            </div>
          </Col>
          <Col>
            <div className="mt-3" style={{float:"right"}}>
              
              <button
                type="button"
                className="btn btn-info btn-sm me-2"
                onClick={onCloseClick}
              >
                Fermer
              </button>
            </div>
          </Col>
          
        </Row>
      </ModalBody>
    </Modal>
  )
}


  }
export default DetailModal
