  handleChargeAll=()=>{
    //this.setState({chargement:false})
    var id=localStorage.getItem("idressource")
    fetch(url+"ressources/"+id)
    .then((response) => response.json())
    .then((data) => {
        $(".owener").css("display","block")
        this.setState({systeme:data.type_systeme_irrigues})
        this.setState({langue:data.langues})
        this.setState({unite:data.unite_administratives})
        this.setState({thematique:data.thematiques})
        this.setState({mot:data.mot_cles})
        this.setState({chargement:true})
        this.setState({localite:data.pays})
    })

  }

  handleChargeFile=()=>{
    //this.setState({chargement:false})
    var id=localStorage.getItem("idressource")
    fetch(url+"ressources/"+id)
    .then((response) => response.json())
    .then((data) => {
        $(".owener").css("display","block")
        data.images.map(
            (element,idx)=>{
                this.setState({image:[...this.state.image,{id:element.id,name:element.file,file:"http://sis.ashia.sn/image/"+element.file}]})
            }
        )
        data.pjointes.map(
            (element,idx)=>{

                taille=parseInt(element.taille)/1024
                this.setState({pjointe:[...this.state.pjointe,{id:element.id,name:element.file,taille:taille+" KO",nom:element.file.split('.')[0].split('-')[1],file:"http://sis.ashia.sn/image/"+element.file,type:element.file.split('.')[1]}]})
               
            }
        )
    })

  }


