    sendPjointe=(data,url,status)=>{
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
          body: JSON.stringify(data)
      };
      fetch(url, requestOptions)
      .then((response) => response.json())
      .then(response =>{console.log("file send,",response)
      this.setState({file_counter:parseInt(this.state.file_counter)-1})
       if(status==0){
    
         this.setState({imgs_attributes:[...this.state.imgs_attributes,{"image_id":response.id}]})
       }
       else{
        this.setState({pjointes_attributes:[...this.state.pjointes_attributes,{"pjointe_id":response.id}]})
       }
       
       if(this.state.file_counter==0 && status==0){
        this.patchFile({"ressource_imgs_attributes":this.state.imgs_attributes})
        $("#show-image").css("display","none")
       }else{
        if(this.state.file_counter==0 && status==1){
            this.patchFile({"ressource_pjointes_attributes":this.state.pjointes_attributes})
            $("#show-pj").css("display","none")
           }
       }


    
     } )
          
       }

    patchFile=(data)=>{
        var id=localStorage.getItem("idressource")
        const requestOptions = {
            method: 'PATCH',
            headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
            body: JSON.stringify(data)
        };
      
          fetch(url+"ressources/"+id, requestOptions)
          .then((response) => response.json())
          .then(response =>{console.log("ok,",response)
         
          this.handleChargeFile()
          $(".owener").css("display","block")
         } ,
         (error) => {
            $(".owener").css("display","block")
         })
      }

    patch=(data)=>{
        var id=localStorage.getItem("idressource")
        const requestOptions = {
            method: 'PATCH',
            headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
            body: JSON.stringify(data)
        };
      
          fetch(url+"ressources/"+id, requestOptions)
          .then((response) => response.json())
          .then(response =>{console.log("ok,",response)
         
          this.handleChargeAll()
          $(".owener").css("display","block")
         } ,
         (error) => {
            $(".owener").css("display","block")
         })
      }
    remove=(data,Url)=>{
        
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
            body: JSON.stringify(data)
        };
      
          fetch(url+Url,requestOptions)
          .then((response) => response.json())
          .then(response =>{console.log("ok,",response)
         
      
         } ,
         (error) => {
            console.log("erreur,",error)
         })
      }
      removeFile=(Url,data)=>{
        
        const requestOptions = {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json','Accept': 'application/json' }
        };
      
          fetch(url+Url,requestOptions)
          .then((response) => response.json())
          .then(response =>{console.log("ok,",response)
    
         } ,
         (error) => {
            console.log("erreur,",error)
         })

          //remove
          $.ajax({
            type: "POST",
            url: 'http://sis.ashia.sn/remove_file/remove.php',
            data: data,
            success: function(data){
            console.log("okk",data);
            },
            error: function(xhr, status, error){
              console.log("hh======<",error);
            }
           });
      }

  
